.spinners img {
  max-width: 50%;
}
#g1,
#g2,
#g3 {
  width: 160px;
  max-width: 100%;
  height: 128px;
  display: block;
  margin: 0 auto;
}
.statBox {
  display: block;
  width: 100%;
  max-width: 290px;
  padding: 10px 20px;
  margin: 0 auto 20px;
  background-color: rgba(220, 220, 220, 0.3);
  border: 1px solid #d1d7df;
  -webkit-box-shadow: 1px 1px 0px white, inset 1px 1px 0px white;
  -moz-box-shadow: 1px 1px 0px white, inset 1px 1px 0px white;
  box-shadow: 1px 1px 0px white, inset 1px 1px 0px white;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.statBox .badge,
.statBox .badge-transparent {
  height: 50px;
  width: 50px;
  padding: 0;
  line-height: 50px;
  font-size: 30px;
  text-align: center;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.25);
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  border-radius: 50px;
}
.statBox .label:empty,
.statBox .badge:empty {
  display: inline-block;
}
.statBox .badge {
  color: #ffffff;
}
.statBox .badge-transparent {
  background-color: transparent;
  font-size: 40px;
}
.statBox .statHolder {
  position: relative;
  display: inline-block;
  float: right;
  top: -5px;
  width: 50%;
  text-align: right;
}
.statBox .statHolder .statTitle {
  display: block;
  font-size: 10px;
  line-height: 30px;
  color: #666666;
  overflow: hidden;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
  white-space: nowrap;
}
.statBox .statHolder .statValue {
  display: block;
  font-size: 25px;
  overflow: hidden;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
  white-space: nowrap;
}
/*
