.breadcrumb-container {
  width: 200%;
  height: 30px;
  line-height: 30px;
  background-color: #f0f0f0;
  border-bottom: 1px solid rgba(220, 220, 222, 0.9);
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.xbreadcrumbs {
  position: relative;
  z-index: 1000;
  clear: both;
  width: 100%;
}
.xbreadcrumbs .breadcrumb-sub-nav {
  position: absolute;
  float: left;
  -webkit-border-radius: 0 0 6px 6px;
  -moz-border-radius: 0 0 6px 6px;
  border-radius: 0 0 6px 6px;
  display: none;
}
.xbreadcrumbs,
.xbreadcrumbs LI,
.breadcrumb-sub-nav,
.breadcrumb-sub-nav LI {
  list-style: none;
  margin: 0;
  padding: 0;
}
.xbreadcrumbs,
.xbreadcrumbs LI {
  float: left;
}
/*  Base style of xBreadcrumbs  */
/*  Top Level  */
.xbreadcrumbs > LI {
  position: relative;
  height: 30px;
  line-height: 30px;
  min-width: 130px;
  padding: 0 20px 0 0;
  background-image: url('../../images/photon/brd_separator@2x.png');
  background-position: center right;
  background-repeat: no-repeat;
  white-space: nowrap;
  background-size: 20px 30px;
}
.xbreadcrumbs > LI A {
  display: block;
  height: 30px;
  padding-right: 10px;
  text-decoration: none;
  color: #666666;
  font-size: 14px;
  line-height: 30px;
  text-shadow: 0px 1px 0px #ffffff;
}
.xbreadcrumbs > LI.current {
  border-right: none;
}
.xbreadcrumbs > LI.current A {
  color: #333333;
  font-size: 14px;
  line-height: 30px;
  text-shadow: 0px 1px 0px #ffffff;
  cursor: default;
}
.xbreadcrumbs > LI:first-child {
  min-width: 0;
  padding: 0 20px 0 0;
}
.xbreadcrumbs > LI:first-child a {
  padding: 0 20px 0 20px;
}
.xbreadcrumbs > LI:last-child {
  min-width: 0;
}
/*  Sub-level  */
.breadcrumb-sub-nav {
  position: absolute;
  width: 100%;
  top: 30px;
  left: -21px;
  background-color: rgba(238, 238, 238, 0.95);
  border: 1px solid #ceced0;
  border-top: 0px solid rgba(220, 220, 222, 0.9);
  overflow: hidden;
  -webkit-box-shadow: inset 1px 0 2px 0 #ffffff;
  -moz-box-shadow: inset 1px 0 2px 0 #ffffff;
  box-shadow: inset 1px 0 2px 0 #ffffff;
}
.breadcrumb-sub-nav LI {
  float: left;
  width: 100%;
  border-right: none;
  height: auto;
  background-image: none;
}
.breadcrumb-sub-nav LI A {
  display: block;
  height: 30px;
  width: 100%;
  padding: 0 20px 0 23px;
  overflow: hidden;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
  white-space: nowrap;
  text-decoration: none;
  color: #666666;
  font-size: 14px;
  line-height: 30px;
  text-shadow: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.breadcrumb-sub-nav LI:hover A {
  color: #333333;
  font-size: 14px;
  line-height: 30px;
  text-shadow: none;
}
.breadcrumb-sub-nav LI:last-child {
  -webkit-border-radius: 0 0 6px 6px;
  -moz-border-radius: 0 0 6px 6px;
  border-radius: 0 0 6px 6px;
}
.breadcrumb-sub-nav LI:last-child A {
  -webkit-border-radius: 0 0 6px 6px;
  -moz-border-radius: 0 0 6px 6px;
  border-radius: 0 0 6px 6px;
}
@media (max-width: 480px) {
  .xbreadcrumbs > LI {
    position: relative;
    left: -5px;
    min-width: 90px;
    padding: 0 30px 0 0;
  }
  .xbreadcrumbs > LI:first-child {
    min-width: 0;
    padding: 0 15px 0 0;
  }
  .xbreadcrumbs > LI:first-child a {
    padding: 0 10px 0 10px;
  }
  .breadcrumb-sub-nav {
    display: none !important;
  }
}
