  width: 100%;
  height: 100%;
  border: none;
  zoom: 1;
  padding: 0;
  outline: none;
}
.el-rte.small textarea {
  min-height: 250px;
}
/*  */
.el-rte iframe {
  width: 100%;
  height: 400px;
  display: block;
  outline: none;
  border: 0 solid;
  padding: 0;
  margin: 0;
  background: #fff;
}
.el-rte.small iframe {
  min-height: 250px;
}
/*   () */
.el-rte .statusbar {
  height: 21px;
  padding: 0 9px;
  font: 11px/21px 'monaco', 'andale mono', 'lucida console', monospace;
  background-color: #eee;
  border-top: 1px solid #ccc;
  border-top: none;
}
.el-rte-rtl .statusbar {
  direction: rtl;
  /*	text-align:right;*/

}
/*  */
.el-rte .tabsbar {
  display: block;
}
.el-rte .tabsbar .tab {
  float: left;
  margin-right: 2px;
  padding: 6px 17px;
  font-size: .82em;
  font-weight: bold;
  color: #777;
  text-align: center;
  border: 1px solid #eee;
  border-top: none;
  background: #fff;
  cursor: default;
}
.el-rte .tabsbar div.active {
  background: #eee;
  color: #444;
}
.el-rte-rtl .tabsbar .tab {
  float: right;
}
/*   */
/* container for elselect */
.el-rte .toolbar ul li.el-select {
  height: 22px;
  width: 120px;
  border: 1px solid #ccc;
  font-size: .82em;
  cursor: default;
  background-position: -475px -30px;
}
.el-rte .toolbar ul li.el-select.hover {
  background-color: #fefefe;
}
.el-rte .toolbar ul li.el-select label {
  height: 19px;
  padding: 3px 5px 0 5px;
  float: none;
  width: 96px;
  /* Joomla 1.6 compat */

}
/* textarea & iframe     */
.el-rte-paste-input {
  width: 99%;
  height: 250px;
  border: 1px solid #ccc;
  padding: 0;
  margin: 0;
  outline: none;
}
/*      */
.el-rte .toolbar ul li div.color-indicator {
  width: 18px;
  height: 4px;
  margin: 16px 0 2px 2px;
  background-color: #000;
}
.el-rte-rtl .toolbar ul li div.color-indicator {
  margin: 16px 2px 2px 0;
}
.el-rte .toolbar .panel-format {
  z-index: 10;
}
/* colors palette */
.el-palette {
  top: 20px;
}
.el-dialogform,
.el-dialogform td {
  font-size: .9em;
}
.el-dialogform select {
  font-size: .9em;
}
.ui-dialog .ui-dialog-buttonpane {
